  height: 32px;
  background-color: #0000;
  border: 1px solid #fe7031;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.25;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.pages__link:hover, .pages__link.active {
