  cursor: pointer;
  margin-left: 12px;
}

.arrow-grey {
  fill: #5f6775;
  cursor: default;
}

.is-hidden {
  display: none;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 3px solid #d6d815;
  border-top-color: #0000;
  border-radius: 50px;
  animation: 1s linear infinite spin;
  position: relative;
  top: 90%;
  left: 47%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
  .spinner {
    width: 70px;
    height: 70px;
    top: 90%;
    left: 47%;
  }
}

@media screen and (min-width: 1280px) {
  .spinner {
    width: 100px;
    height: 100px;
    top: 90%;
    left: 46%;
  }
}

@keyframes spin {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.spinner-bg {
  border-width: 10px;
}

.spinner-alert {
  border-color: #0000 #fd5103 #fd5103;
}

.visually-hidden-spinner {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  opacity: 0;
  pointer-events: none;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

